            >
              <i class="fa fa-file-text" aria-hidden="true"></i> 
            </span>
            <span
              class="cursordefault"
              v-bind:class="{blue:(mine==3)}"
              @mouseover="changeMine(3,$event)"
            >
              <i class="fa fa-check-square-o" aria-hidden="true"></i> 
            </span>
          </div>
          <div v-if="mine==1" id="userCenter">
            <div class="whiteBox">
              <div class="column" v-if="loginMessage==0">
                <div id="loginTitle"></div>
                <div class="inputLine" v-bind:class="{focusing:focusing==0}">
                  <div class="blueBox">
                    <i class="fa fa-user" aria-hidden="true"></i> :
                  </div>
                  <input
                    @click.stop="changeFocus(0)"
                    class="loginInput"
                    id="loginName"
                    v-model="username"
                    placeholder="account"
                  >
                </div>
                <div class="inputLine" v-bind:class="{focusing:focusing==1}">
                  <div class="blueBox" @click.stop="changeFocus(1)">
                    <i class="fa fa-key" aria-hidden="true"></i> :
                  </div>
                  <input
                    @click.stop="changeFocus(1)"
                    class="loginInput"
                    id="loginPassword"
                    type="password"
                    v-model="password"
                    placeholder="password"
                  >
                </div>
                <button id="loginButton" @click.stop="login(username,password)">{{loginButton}}</button>
              </div>
              <div class="column" v-if="loginMessage!=0">
                <div id="userMessage">{{' : ' + userRank}}</div>
                <button id="logOut" @click.stop="logOut()"></button>
              </div>
            </div>
          </div>
          <div v-if="mine==2" id="takeNews">
            <div class="whiteBox column">
              <li v-for="i in takeNews" class="selectable">{{i.content}}</li>
            </div>
          </div>
          <div v-if="mine==3" id="myDeal">
            <div class="whiteBox column">
              <li v-for="i in myDeal" class="selectable">{{i.content}}</li>
            </div>
          </div>
        </div>
      </div>
      <div id="enterpriseOthers">
        <div id="otherHeader">
          <div class="flexbox2">
            <span
              class="cursordefault"
              v-bind:class="{blue:(others==1)}"
              @mouseover="changeOthers(1,$event)"
            >
