            </span>
            <span
              class="cursordefault"
              v-bind:class="{blue:(others==2)}"
              @mouseover="changeOthers(2,$event)"
            >
              <i class="fa fa-file-text" aria-hidden="true"></i> 
            </span>
            <span
              class="cursordefault"
              v-bind:class="{blue:(others==3)}"
              @mouseover="changeOthers(3,$event)"
            >
              <i class="fa fa-check-square-o" aria-hidden="true"></i> 
            </span>
          </div>
          <div v-if="others==1" id="recentActive">
            <div class="whiteBox">
              <div class="whiteBox column">
                <li v-for="i in takeActive" class="selectable">{{i.content}}</li>
              </div>
            </div>
          </div>
          <div v-if="others==2" id="communityActive">
            <div class="whiteBox">
              <div class="whiteBox column">
                <li v-for="i in communityActive" class="selectable">{{i.content}}</li>
              </div>
            </div>
          </div>
          <div v-if="others==3" id="other">
            <div class="whiteBox">
              <div class="whiteBox column">
                <li v-for="i in other" class="selectable">{{i.content}}</li>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id="advertisePlace"></div>
    </div>
  </div>
</template>

