  },
  methods: {
    changeMine: function(n, event) {
      this.mine = n;
      //event.target.style.color='red';
    },
    changeOthers: function(n, event) {
      this.others = n;
    },
    changeFocus: function(n) {
      this.focusing = n;
    },
    logOut: function() {
      this.loginMessage = 0;
      this.axios.get(this.HOST + "/user/loginOut").then(res => {});
    },
    login: function(a, b) {
      let username = this.username;
      let password = this.password;
      let that = this;
      let url = this.HOST + "/user/login";
      let params = Qs.stringify({
        username: username, //String
        password: password //String
      });
      this.axios({
        method: "post",
        url: url,
        data: params
      })
        .then(response => {
          if (response.data.result == "error") {
            let message = response.data.message;
            that.$message.error({
              showClose: true,
              message: message,
              duration: 2000
            });
          } else {
            // that.defined.setRank(response.data.message.rank);
            // localStorage.setItem("userRank", response.data.message.rank);
            that.$message.success({
