            });
            //
          }
        })
        .catch(response => {
          // console.log(response);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        });
    },
    getPermission: function() {
      this.axios.get(this.HOST + "/user/getPermission").then(res => {
        this.loginMessage = res.data.message;
        if (res.data.message == 1) this.userRank = "";
        if (res.data.message == 2) this.userRank = "";
        if (res.data.message == 3) this.userrANK = "";
      });
